body {
  margin: 0;
  font-family: 'Merriweather Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* NavBar.css */
nav {
  width: 100vw;
  display: flex;
  justify-content: space-between;
  background-color: #001219;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 101;
}
nav h1 {
  color: #fff;
  margin-left: 40px;
}
nav a {
  text-decoration: none;
}
nav span {
  /* color: #ee9b00; */
  cursor: pointer;
  margin-left: 2.5rem;
  font-size: 0.85rem;
  text-transform: uppercase;
}
nav span:hover {
  color: #fff;
  border-bottom: 2px solid #ee9b00;
  padding-bottom: 5px;
}
span #HomeLight,
#MoviesLight,
#TrendingLight,
#PricingLight {
  color: #fff;
}
span #HomeLight:hover,
#MoviesLight:hover,
#TrendingLight:hover,
#PricingLight:hover {
  border-bottom: 2px solid #ff206e;
}
#navBarColor {
  background-color: #38bdab;
  transition: 0.6s;
}
.nav-options {
  display: flex;
  align-items: center;
}
#heading {
  color: #ff206e;
}
#navBarColor span:hover {
  border-bottom: 2px solid #ff206e;
  padding-bottom: 5px;
}
.input-group {
  display: flex;
  align-items: center;
}
#Color-switcher {
  position: relative;
  margin-right: 40px;
  background-color: #413e44;
  width: 70px;
  height: 30px;
  border-radius: 20px;
}
#Color-switcher-mover {
  position: absolute;
  left: 5px;
  top: 3px;
  background-color: #fff;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  transition: 0.6s;
}
#Color-switcher-moved {
  position: absolute;
  background-color: #fff;
  left: 40px;
  top: 3px;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  transition: 0.6s;
}
.input-group input {
  position: relative;
  width: 323px;
  height: 4vh;
  border-radius: 16px;
  border: none;
  margin-right: 30px;
}
.input-group input[type="text"] {
  padding-left: 17px;
  font-size: 1rem;
  color: black;
}
.input-group input::-webkit-input-placeholder {
  color: black;
}
.input-group input::placeholder {
  color: black;
}
.input-group input:focus {
  outline: none;
}
#search {
  position: absolute;
  margin-left: 310px;
}

/* ####################################################################### */

@media screen and (max-width: 767px) {
  /* Media query for screens 767px and below */

  nav {
    display: block;
    flex-direction: column;
    align-items: center;
    padding: 0px 0.7rem; /* Add some spacing for readability */
    position: -webkit-sticky;
    position: sticky;
    top: 0;
  }

  nav h1 {
    margin: 20px 10px;
    font-size: 0.9rem;
  }

  nav span {
    margin: 0.5rem;
    font-size: 0.7rem;
  }

  .nav-options {
    flex-direction: row;
    flex: 1 1 6rem;
    justify-content: center;
  }

  .nav-options.active {
    display: flex; /* Show the navigation options when active */
  }

  #Color-switcher {
    position: relative;
    margin-right: 40px;
    background-color: #413e44;
    width: 60px;
    height: 20px;
    border-radius: 20px;
  }
  #Color-switcher-mover {
    position: absolute;
    left: 5px;
    top: 3px;
    background-color: #fff;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    transition: 0.6s;
  }
  #Color-switcher-moved {
    position: absolute;
    background-color: #fff;
    left: 40px;
    top: 3px;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    transition: 0.6s;
  }

  .input-group {
    flex-direction: row;
    align-items: center;
    padding-bottom: 1rem;
  }

  .input-group input {
    width: 60%;
    height: 1rem;
    margin-left: 2rem;
    font-size: 12px;
  }

  .custom-placeholder::-webkit-input-placeholder {
    font-size: 12px;
    font-weight: lighter;
    color: black;
    padding-left: 1rem;
  }

  .custom-placeholder::placeholder {
    font-size: 12px;
    font-weight: lighter;
    color: black;
    padding-left: 1rem;
  }

  input[type="text"].custom-placeholder {
    font-size: 12px; /* to Change input font size when Search for something */
    font-weight: lighter;
    color: black;
    padding-left: 2rem;
  }

  #search {
    /* position: static; */
    margin: 0px 0px 0px 2.2rem;
    font-size: 1.2rem;
  }
}

/* Videos.css  */
body {
  overflow-x: hidden;
}
.movies-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.mainBgColor {
  background-color: #0c1e25;
  width: 100%;
}
.secondaryBgColor {
  background-color: #fff;
  width: 100%;
}
.movies-container img {
  width: 350px;
  height: 550px;
  margin: 25px 0px 4px 15px;
  border-radius: 18px;
}
.mainColor {
  color: #fff;
  display: flex;
  justify-content: center;
  font-size: 1rem;
}
#Nothing {
  display: none;
}
.secondaryColor {
  color: #ff206e;
  width: 90%;
  display: flex;
  justify-content: center;
  font-size: 1rem;
}
.movies-container h3,
.movies-container img {
  cursor: pointer;
  transition: 0.7s;
}
.movies-container img:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
#container {
  position: relative;
}
#hide {
  display: none;
}
#smaller-Text {
  width: 100%;
  font-size: 14px;
  display: flex;
  justify-content: center;
}
#playIcon {
  position: absolute;
  bottom: 70px;
  left: 20px;
  visibility: visible;
  z-index: 100;
  cursor: pointer;
}
#Exit1 {
  position: absolute;
  margin-left: 800px;
  margin-top: 67px;
  z-index: 999;
}
#Nothing {
  color: green;
}
#NoContainer {
  opacity: 0.35;
}
.DarkTheme {
  color: #fff;
}
.LightThemeClose {
  color: #ff206e;
}

/* Mobile-responsive styles ############################################################# */

@media (max-width: 768px) {
 

  .mainColor,
  .secondaryColor {
    font-size: 0.9rem; /* Reduce font size for smaller screens */
  }
}

.mainBgColor {
  background-color: #0c1e25;
  width: 100%;
}

.secondaryBgColor {
  background-color: #fff;
  width: 100%;
}

.movies-container img {
  /* Keep your existing styles for larger screens */
  width: 18rem;
  height: 31rem;
  margin: 25px 0px 4px 15px;
  border-radius: 18px;
}

.mainColor {
  color: #fff;
  display: flex;
  justify-content: center;
  font-size: 1rem;
}

#Nothing {
  display: none;
}

.secondaryColor {
  color: #ff206e;
  width: 90%;
  display: flex;
  justify-content: center;
  font-size: 1rem;
}

/* TrailerMovi.css */
.player {
  position: absolute;
  margin-top: 120px;
  z-index: 100;
}
.Container {
  display: none;
  position: absolute;
  z-index: 100;
}
#TrailerMovie-name-dark {
  display: flex;
  width: 100%;
  justify-content: center;
  position: absolute;
  top: -100px;
  font-size: 2.5rem;
  color: #ee9b00;
}
#TrailerMovie-name-light {
  display: flex;
  width: 100%;
  justify-content: center;
  position: absolute;
  top: -100px;
  font-size: 2.5rem;
  color: #ff206e;
}

/* Mobile-responsive styles */
@media (max-width: 768px) {
  .player {
    margin-top: 80px; /* Adjust margin for smaller screens */
  }

  #TrailerMovie-name-dark,
  #TrailerMovie-name-light {
    font-size: 1.5rem; /* Reduce font size for smaller screens */
    top: -60px; /* Adjust position for smaller screens */
  }
}

/* pricing.css */

.backgroun-Color-Main {
